<script>
import axios from "axios";
import Pfp from "../components/Pfp.vue";
export default {
     data() {
          return {
               landing: false,
               loggedIn: false
          };
     },
     methods: {
          getPath() {
               if (this.$route.name == "Landing") {
                    this.landing = true;
               } else {
                    this.landing = false;
               }
               if (!this.$store.getters.checkTokenNull) {
                    this.loggedIn = true;
               } else {
                    this.loggedIn = false;
               }
               if (this.landing) {
                    $("#tabs").css("background-color", "transparent");
                    $(".tab").css("color", "white");
               }
          }
     },
     mounted() {
          this.getPath();
     },
     components: {
          Pfp
     }
};
